* {
   padding: 0px;
   margin: 0px;
   border: 0px;
}
*,
*:before,
*:after {
   -moz-box-sizing: border-box;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}
:focus,
:active {
   outline: none;
}
a:focus,
a:active {
   outline: none;
}
aside,
nav,
footer,
header,
section {
   display: block;
}
html,
body {
   height: 100%;
}
body {
   line-height: 1; /*text-rendering: optimizeLegibility;*/
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
}
input::-ms-clear {
   display: none;
}
button {
   background-color: transparent;
   color: inherit; /* Сохраняет цвет текста кнопки */
   border: none; /* Удаляет границу кнопки */
   padding: 0; /* Убирает отступы внутри кнопки */
   cursor: pointer; /* Устанавливает вид курсора при наведении */
}
button::-moz-focus-inner {
   padding: 0;
   border: 0;
}
a,
a:visited {
   text-decoration: none;
}
a:hover {
   text-decoration: none;
}
// ul li {
//    list-style: none;
// }
img {
   vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
   font-weight: inherit;
   font-size: inherit;
}

